<template>
  <div>
    <canvas
      id="renderCanvas"
      touch-action="none"
      style="width: 100%; height: 100%"
    ></canvas>
    <div id="log"></div>
  </div>
</template>

<script>
import {
  Engine,
  Scene,
  ArcRotateCamera,
  Vector3,
  Layer,
  SceneLoader,
  VideoTexture,
  ShadowGenerator,
  DirectionalLight,
  MeshBuilder,
  WebXRPlaneDetector,
  Matrix,
  Vector2,
  PolygonMeshBuilder,
  StandardMaterial,
  Color3,
  Quaternion,
} from "@babylonjs/core";
import "@babylonjs/loaders";
import "@babylonjs/inspector";
import { ShadowOnlyMaterial } from "@babylonjs/materials";

export default {
  name: "BabylonCpn",
  data() {
    return {
      engine: null,
      scene: null,
      camera: null,
      shadowGenerator: null,
      model: null,
      // xr: null,
      // planeDetected: false,
      planes: [],
    };
  },
  mounted() {
    this.initializeBabylon();
  },

  methods: {
    async initializeBabylon() {
      // Lấy thẻ canvas
      var canvas = document.getElementById("renderCanvas");

      // Khởi tạo Babylon.js Engine
      this.engine = new Engine(canvas, true);

      // Tạo scene
      this.scene = await this.createScene(canvas);

      // Render loop
      this.engine.runRenderLoop(() => {
        this.scene.render();
      });

      // Resize
      window.addEventListener("resize", () => {
        this.engine.resize();
      });

      // Thiết lập kích thước canvas
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
      this.logMessage(
        "Canvas width: " + canvas.width + " Canvas height: " + canvas.height
      );
    },

    async createScene(canvas) {
      var scene = new Scene(this.engine);

      // Thêm ánh sáng
      this.addLight(scene);

      // Thêm camera
      this.camera = this.addCamera(scene, canvas);

      // Thêm video layer

      // Thiết lập AR
      await this.setupXR(scene);

      // Tạo mặt đất
      // const ground = MeshBuilder.CreatePlane("ground", { size: 2000 }, scene);
      // ground.rotation.x = Math.PI / 2;
      // ground.material = new ShadowOnlyMaterial("shadowOnly", scene);
      // ground.receiveShadows = true;
      // ground.position.y = 0;

      return scene;
    },

    addLight(scene) {
      // Xóa ánh sáng mặc định nếu có
      scene.lights.forEach((light) => light.dispose());

      // Tạo ánh sáng mới
      var light = new DirectionalLight(
        "dirLight",
        new Vector3(-2, -3, 1),
        scene
      );
      light.position = new Vector3(6, 9, 3);

      // Tạo Shadow Generator
      this.shadowGenerator = new ShadowGenerator(1024, light);
      this.shadowGenerator.useBlurExponentialShadowMap = true;
      this.shadowGenerator.blurKernel = 32;
    },

    addCamera(scene, canvas) {
      // Điều chỉnh vị trí và góc quay của camera để có góc nhìn như trong hình
      const camera = new ArcRotateCamera(
        "Camera",
        -Math.PI / 2,
        Math.PI / 2,
        12,
        Vector3.Zero(),
        scene
      );
      camera.attachControl(canvas, true);
      return camera;
    },

    logMessage(message) {
      const logDiv = document.getElementById("log");
      logDiv.innerHTML += message + "<br>";
    },

    async loadModel(scene, position) {
      await SceneLoader.ImportMesh(
        "",
        "/models/yasuo/",
        "scene.gltf",
        scene,
        (meshes) => {
          // Đặt vị trí của mô hình nếu cần
          meshes.forEach((mesh) => {
            mesh.position = position;
            console.log("Mesh position set to:", position);

            // Thêm các mesh vào Shadow Generator
            this.shadowGenerator.addShadowCaster(mesh);
          });
          this.model = meshes[0]; // Giả định mô hình chính là mesh đầu tiên
          console.log("Model loaded and placed at position:", position);
        },
        null,
        (scene, message) => {
          console.error("Error loading model:", message);
        }
      );
    },

    async setupXR(scene) {
      try {
        var xr = await scene.createDefaultXRExperienceAsync({
          uiOptions: {
            sessionMode: "immersive-ar",
            referenceSpaceType: "local-floor",
          },
          optionalFeatures: true,
        });

        const fm = xr.baseExperience.featuresManager;

        const xrPlanes = fm.enableFeature(WebXRPlaneDetector.Name, "latest");

        xrPlanes.onPlaneAddedObservable.add((plane) => {
          // plane.polygonDefinition.push(plane.polygonDefinition[0]);
          // var polygon_triangulation = new PolygonMeshBuilder(
          //   "name",
          //   plane.polygonDefinition.map((p) => new Vector2(p.x, p.z)),
          //   scene
          // );
          // var polygon = polygon_triangulation.build(false, 0.01);
          // plane.mesh = polygon;
          // this.planes[plane.id] = plane.mesh;
          // const mat = new StandardMaterial("mat", scene);
          // mat.alpha = 0.5;
          // // pick a random color
          // mat.diffuseColor = Color3.Random();
          // polygon.createNormals();
          // plane.mesh.material = mat;
          // plane.mesh.rotationQuaternion = new Quaternion();
          // plane.transformationMatrix.decompose(
          //   plane.mesh.scaling,
          //   plane.mesh.rotationQuaternion,
          //   plane.mesh.position
          // );
        });

        xrPlanes.onPlaneUpdatedObservable.add((plane) => {
          // let mat;
          // if (plane.mesh) {
          //   // keep the material, dispose the old polygon
          //   mat = plane.mesh.material;
          //   plane.mesh.dispose(false, false);
          // }
          // const some = plane.polygonDefinition.some((p) => !p);
          // if (some) {
          //   return;
          // }
          // plane.polygonDefinition.push(plane.polygonDefinition[0]);
          // var polygon_triangulation = new PolygonMeshBuilder(
          //   "name",
          //   plane.polygonDefinition.map((p) => new Vector2(p.x, p.z)),
          //   scene
          // );
          // var polygon = polygon_triangulation.build(false, 0.01);
          // polygon.createNormals();
          // plane.mesh = polygon;
          // this.planes[plane.id] = plane.mesh;
          // plane.mesh.material = mat;
          // plane.mesh.rotationQuaternion = new Quaternion();
          // plane.transformationMatrix.decompose(
          //   plane.mesh.scaling,
          //   plane.mesh.rotationQuaternion,
          //   plane.mesh.position
          // );
        });

        xrPlanes.onPlaneRemovedObservable.add((plane) => {
          // if (plane && this.planes[plane.id]) {
          //   this.planes[plane.id].dispose();
          // }
        });

        xr.baseExperience.sessionManager.onXRSessionInit.add(() => {
          // this.planes.forEach((plane) => plane.dispose());
          // this.planes.length = 0;
        });
      } catch (error) {
        console.error("WebXR Plane Detector not supported:", error);
        this.logMessage("WebXR Plane Detector not supported: " + error);
      }
    },
  },
};
</script>

<style>
body {
  margin: 0;
  overflow: hidden;
}
canvas {
  width: 100%;
  height: 100%;
}
</style>
